body {
  min-height: 100vh;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  font-family: "Helvetica", "Arial", sans-serif;
  color: #000000;
  text-rendering: optimizeLegibility;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: #000000;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.header,
.footer {
  flex-shrink: 0;
}

main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Helvetica";
  src: url("../fonts/helvetica-regular.woff2") format("woff2"), url("../fonts/helvetica-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Helvetica";
  src: url("../fonts/helvetica-bold.woff2") format("woff2"), url("../fonts/helvetica-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.container {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 200px;
}
@media (max-width: 1100px) {
  .container {
    padding: 0 150px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.container--goods {
  padding: 0 200px 0 100px;
}
@media (max-width: 1023px) {
  .container--goods {
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .container--goods {
    padding: 0 16px;
  }
}

@media (max-width: 767px) {
  .container--header {
    background-color: #ffffff;
  }
}

.header {
  border-bottom: 1px solid #c8c8c8;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .header {
    border-bottom: none;
    position: relative;
    z-index: 2;
  }
}
.header__wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 25px 0 22px;
}
@media (max-width: 767px) {
  .header__wrapper {
    z-index: 2;
    padding: 25px 0 2px;
  }
}
.header__logo {
  margin: 0 0 3px 0;
}
@media (max-width: 767px) {
  .header__nav {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transform: translateY(-100%);
    transition: transform 0.8s ease-in-out, height 0.8s ease-in-out, opacity 0.8s ease-in-out;
    z-index: -1;
  }
}
.header__tools {
  margin: 0 0 0 15px;
  padding: 0;
}
@media (max-width: 767px) {
  .header__tools {
    margin: 0 24px 0 auto;
  }
}

.navigation {
  max-width: 280px;
  margin: 0 15px 0 0;
  width: 100%;
}
.navigation__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (max-width: 767px) {
  .navigation__list {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
  }
}
.navigation__item:not(:last-child) {
  margin: 0 40px 3px 0;
}
@media (max-width: 767px) {
  .navigation__item:not(:last-child) {
    margin: 0;
  }
}
.navigation__item {
  margin: 0 0 3px 0;
}
@media (max-width: 767px) {
  .navigation__item {
    width: 100%;
  }
}
.navigation__link {
  display: block;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
}
@media (max-width: 767px) {
  .navigation__link {
    width: 100%;
    font-size: 18px;
    line-height: 22px;
    padding: 15px 0;
  }
}
.navigation__link:hover {
  text-decoration: underline;
}
@media (max-width: 767px) {
  .navigation__link:hover {
    text-decoration: none;
    background-color: #f1f1f1;
  }
}
@media (max-width: 767px) {
  .navigation__link:active {
    font-weight: 600;
    background-color: transparent;
  }
}
.navigation__link--active {
  text-decoration: underline;
}
@media (max-width: 767px) {
  .navigation {
    margin: 0;
    max-width: 100%;
    width: 100%;
    padding: 0 16px 25px;
  }
}

.tools {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 170px;
}

.tools__item:not(:last-child) {
  margin: 0 30px 3px 0;
}

.tools__item {
  position: relative;
  margin: 0 0 3px 0;
}
.tools__item span {
  position: absolute;
  top: -6px;
  right: -8px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #000000;
  color: #ffffff;
  font-weight: 400;
  font-size: 10px;
  line-height: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  .tools__item {
    display: none;
  }
}

@media (max-width: 767px) {
  .tools__item--bag span {
    display: none;
  }
}
@media (max-width: 767px) {
  .tools__item--bag {
    display: block;
  }
}

.tools__link {
  transition: color 0.3s ease;
}
.tools__link svg {
  fill: none;
  width: 20px;
  height: 20px;
}
@media (max-width: 767px) {
  .tools__link svg {
    width: 28px;
    height: 28px;
  }
}

.tools__link:hover {
  color: #6e6e6e;
  transition: color 0.3s ease;
}

.breadcrumbs__wrapper {
  display: flex;
  align-items: center;
  padding: 50px 0 40px;
}
@media (max-width: 767px) {
  .breadcrumbs__wrapper {
    padding: 22px 0 32px;
  }
}
.breadcrumbs__list {
  list-style: none;
  padding: 0;
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.breadcrumbs__item:not(:last-child) {
  position: relative;
}
.breadcrumbs__item:not(:last-child)::after {
  content: "/";
  position: absolute;
  right: 0;
}
.breadcrumbs__item:not(:last-child) a {
  color: #6e6e6e;
}
.breadcrumbs__item {
  padding: 0 10px 0 0;
  margin: 0 10px 0 0;
}
.breadcrumbs__item a {
  color: #000000;
  font-weight: 500;
}
.breadcrumbs__link:hover {
  text-decoration: underline;
}

.order__wrapper {
  display: flex;
  flex-direction: column;
}
.order__header {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  margin: 0 0 60px;
}
@media (max-width: 767px) {
  .order__header {
    margin: 0 0 32px;
  }
}
.order__title {
  font-weight: 400;
  font-size: 40px;
  line-height: 42px;
  color: #000000;
  padding: 0;
  margin: 0;
}
@media (max-width: 767px) {
  .order__title {
    width: 100%;
    margin: 0 0 10px;
    font-size: 28px;
    line-height: 30px;
  }
}
.order__have-account {
  padding: 0 0 2px;
  margin: 0 0 0 20px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #6e6e6e;
}
@media (max-width: 767px) {
  .order__have-account {
    margin: 0;
    padding: 0;
    font-size: 14px;
    line-height: 18px;
  }
}
.order__account-link {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  margin: 0 0 0 5px;
  padding: 0 0 2px;
}
@media (max-width: 767px) {
  .order__account-link {
    padding: 0;
    font-size: 14px;
    line-height: 18px;
  }
}
.order__account-link:hover {
  text-decoration: underline;
}
.order__goods-title {
  display: block;
  font-weight: 400;
  font-size: 28px;
  line-height: 30px;
  color: #000000;
  margin: 0 0 30px;
}
@media (max-width: 767px) {
  .order__goods-title {
    font-size: 24px;
    line-height: 30px;
    max-width: 180px;
    margin: 0 0 24px;
  }
}

.cards__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  border-bottom: 0.5px solid #c8c8c8;
}

.card {
  border-top: 0.5px solid #c8c8c8;
}
.card__wrapper {
  padding: 25px 32px 25px 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 10px;
  position: relative;
  align-items: flex-start;
}
@media (max-width: 767px) {
  .card__wrapper {
    padding: 16px 0;
    display: flex;
    flex-direction: column;
  }
}
.card__button {
  position: absolute;
  top: 23px;
  right: 4px;
}
.card__image {
  display: grid;
  grid-template-columns: 0.389fr 1fr;
  grid-template-rows: auto auto;
  grid-column-gap: 10px;
}
@media (max-width: 767px) {
  .card__image {
    grid-template-columns: 90px 1fr;
    grid-row-gap: 10px;
  }
}
.card__image picture {
  grid-row: 1/-1;
  grid-column: 1/2;
}
@media (max-width: 767px) {
  .card__image picture {
    grid-row: 1/2;
  }
}
@media (max-width: 767px) {
  .card__image img {
    width: 90px;
    height: 111px;
  }
}
.card__title {
  display: block;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
  max-width: 91%;
  margin: 0 0 15px;
}
@media (max-width: 767px) {
  .card__title {
    font-size: 14px;
    line-height: 18px;
    max-width: 91%;
  }
}
.card__title:hover {
  text-decoration: underline;
}
.card__info {
  display: flex;
  flex-direction: column;
}
@media (max-width: 767px) {
  .card__info {
    grid-column: 1/-1;
    grid-row: 2/-1;
  }
}
.card__description {
  margin: 0 0 10px;
  padding: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #6e6e6e;
}
@media (max-width: 767px) {
  .card__description {
    margin: 0 0 4px;
  }
}
.card__description span {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #000000;
  margin: 0 0 0 5px;
}
@media (max-width: 767px) {
  .card__description:last-of-type {
    margin: 0 0 12px;
  }
}
@media (max-width: 767px) {
  .card__price {
    align-self: center;
  }
}

.card--deleted .card__wrapper {
  display: flex;
  align-items: center;
  padding: 25px 0;
}
@media (max-width: 767px) {
  .card--deleted .card__wrapper {
    padding: 16px 0;
  }
}
.card--deleted .card__wrapper .card__content {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background-color: #f1f1f1;
  padding: 15px 52px 15px 25px;
}
@media (max-width: 767px) {
  .card--deleted .card__wrapper .card__content {
    padding: 12px 15px 12px 8px;
    flex-direction: column;
    align-items: flex-start;
  }
}
.card--deleted .card__wrapper .card__button {
  top: 50%;
  right: 40px;
}
@media (max-width: 767px) {
  .card--deleted .card__wrapper .card__button {
    top: 36px;
    right: 18px;
  }
}
.card--deleted .card__wrapper p {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #6e6e6e;
}
@media (max-width: 767px) {
  .card--deleted .card__wrapper p {
    font-size: 14px;
    line-height: 18px;
    max-width: 91%;
    margin: 0 0 4px;
  }
}
.card--deleted .card__wrapper p a {
  font-weight: 600;
  color: #6e6e6e;
}
.card--deleted .card__wrapper p a:hover {
  text-decoration: underline;
}
.card--deleted .card__wrapper .card__link-restore {
  display: block;
  margin: 0 0 0 25px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
}
@media (max-width: 767px) {
  .card--deleted .card__wrapper .card__link-restore {
    margin: 0;
  }
}
.card--deleted .card__wrapper .card__link-restore:hover {
  text-decoration: underline;
}

.price {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .price {
    width: 100%;
  }
}
.price__content {
  display: flex;
  flex-direction: column;
}
.price__old {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #6e6e6e;
  text-decoration: line-through;
}
.price__now {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
  max-width: 80px;
}
@media (max-width: 767px) {
  .price__now {
    font-size: 16px;
    line-height: 21px;
  }
}
.price__now span {
  font-weight: 600;
}

.total {
  border: 1px solid #6e6e6e;
}
.total__wrapper {
  padding: 30px 20px;
}

.burger-button {
  cursor: pointer;
  display: none;
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  width: 32px;
  height: 32px;
  transition: color 0.3s ease;
}
.burger-button svg {
  width: 32px;
  height: 32px;
}
@media (max-width: 767px) {
  .burger-button {
    display: block;
  }
}

.burger-button:hover {
  color: #6e6e6e;
  transition: color 0.3s ease;
}

.opened {
  height: 100vh;
  opacity: 1;
  background-color: #ffffff;
  transform: translateY(11%);
  transition: transform 0.8s ease-in-out, height 0.8s ease-in-out, opacity 0.8s ease-in-out;
}

.size {
  list-style: none;
  margin: 0 0 10px;
  padding: 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .size {
    margin: 0 0 16px;
  }
}
.size__item:not(:last-child) {
  margin: 0 5px 0 0;
}
.size__item {
  cursor: pointer;
  width: 30px;
  height: 30px;
  border-radius: 6px;
  text-transform: uppercase;
  background-color: #f1f1f1;
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
}
.size__item:hover {
  background-color: #c8c8c8;
  color: #000000;
}
.size__item--active {
  background-color: #000000;
  color: #ffffff;
}
.size__item--disabled {
  color: #c8c8c8;
  pointer-events: none;
}

.color {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (max-width: 767px) {
  .color {
    margin: 0 0 16px;
  }
}
.color__item {
  width: 20px;
  height: 20px;
  margin: 0 7px 0 0;
}
@media (max-width: 767px) {
  .color__item {
    margin: 0 16px 0 0;
  }
}
.color__item label {
  display: block;
  position: relative;
  cursor: pointer;
  user-select: none;
}

.radio + label::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: block;
  padding: 2px;
}
.radio:hover + label::before {
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 0px 1px #000000;
}
.radio:checked + label::before {
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 0px 1px #000000;
}

.radio--black + label::before {
  background-color: #444444;
}

.radio--blue + label::before {
  background-color: #106da1;
}

.radio--brown + label::before {
  background-color: #ae6d0c;
}

.button {
  cursor: pointer;
  border: none;
  background-color: transparent;
}
.button--close::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 1.5px;
  background-color: #000000;
  transform: rotate(45deg);
  top: 0;
  left: 0;
}
@media (max-width: 767px) {
  .button--close::after {
    width: 17px;
  }
}
.button--close::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 1.5px;
  background-color: #000000;
  transform: rotate(-45deg);
  top: 0;
  left: 0;
}
@media (max-width: 767px) {
  .button--close::before {
    width: 17px;
  }
}
.button--reduce::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 1.5px;
  background-color: #000000;
  top: 0;
  left: 0;
}
.button--add::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 1.5px;
  background-color: #000000;
  top: 0;
  left: 0;
}
.button--add::before {
  content: "";
  position: absolute;
  width: 12px;
  height: 1.5px;
  background-color: #000000;
  transform: rotate(90deg);
  top: 0;
  left: 0;
}

.quantity {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  margin: 0 30px;
  background-color: #f1f1f1;
  border-radius: 5px;
}
@media (max-width: 767px) {
  .quantity {
    margin: 0 8px;
  }
}
.quantity__button {
  position: relative;
}
.quantity span {
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #000000;
  margin: 0 30px;
}
@media (max-width: 767px) {
  .quantity span {
    margin: 0 27px;
  }
}